import React, { Component } from 'react';
import { Card } from 'antd';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import './ChartBarHorizontalCSS.css';
am4core.useTheme(am4themes_animated);
class ChartBarHorizontal extends Component {
    constructor(props){
        super(props);
      
    }
    componentDidMount() {
        let chart = am4core.create(this.props.idDiv, am4charts.XYChart);
        // chart.width = am4core.percent(100);
        chart.height = am4core.percent(100);

        // Add data
		chart.data = this.props.data;
		
		chart.legend = new am4charts.Legend();
		chart.legend.position = "right";
		
		// Create axes
		let categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
		categoryAxis.dataFields.category = "channel";
		categoryAxis.renderer.grid.template.opacity = 0;
		categoryAxis.renderer.minGridDistance = 1;
		categoryAxis.renderer.inversed = true;
		
		let valueAxis = chart.xAxes.push(new am4charts.ValueAxis());
		valueAxis.min = 0;
		valueAxis.renderer.grid.template.opacity = 0;
		valueAxis.renderer.ticks.template.strokeOpacity = 0.5;
		valueAxis.renderer.ticks.template.stroke = am4core.color("#495C43");
		valueAxis.renderer.ticks.template.length = 10;
		valueAxis.renderer.line.strokeOpacity = 0.5;
		valueAxis.renderer.baseGrid.disabled = true;
		valueAxis.renderer.minGridDistance = 40;
		
		// Create series
		function createSeries(field, name) {
			let series = chart.series.push(new am4charts.ColumnSeries());
			series.dataFields.valueX = field;
			series.dataFields.categoryY = "channel";
			series.stacked = true;
            series.name = name;
            series.columns.template.tooltipText = "[bold]{valueX}[/]";
		  
		  	// let labelBullet = series.bullets.push(new am4charts.LabelBullet());
		  	// labelBullet.locationX = 0.5;
		  	// labelBullet.label.text = "{valueX}";
            // labelBullet.label.fill = am4core.color("#fff");
		}
		
		createSeries("Executed", "Executed");
		createSeries("Expected", "Expected");
    }

    componentWillUnmount() {
        if (this.chart) {
            this.chart.dispose();
        }
    }

    render()    {
        return(

            <Card title={this.props.cardTitle} bordered={false} style={{ width: '100%', height:'100%' }} className="Card">
                <div id={this.props.idDiv} style={{ width: "100%", height:"200px"}}></div>
            </Card>

        );
    }
}
export default ChartBarHorizontal