function callback(key) {

    console.log(key);
}

function getDate(){
    let date;
    let data = [];
    let value = 50;
    for(let i = 0; i < 20; i++){
        date= new Date();
        date.setHours(0,0,0,0);
        date.setDate(i);
        value -= Math.round((Math.random() < 0.5 ? 1 : -1) * Math.random() * 10);
        data.push({date:date, value: value});
    }
    return data;
}
